import React from 'react'
import './style.css'
import Product1 from '../../assets/images/banner3.png'
import Rating from '@mui/material/Rating';
import { Button, Link } from '@mui/material';
import ShoppingCartOutlinedIcon from '@mui/icons-material/ShoppingCartOutlined';
import FavoriteBorderOutlinedIcon from '@mui/icons-material/FavoriteBorderOutlined';
import CompareArrowsOutlinedIcon from '@mui/icons-material/CompareArrowsOutlined';
import VisibilityOutlinedIcon from '@mui/icons-material/VisibilityOutlined';

const Product = (props) => {
  return (
    <div className='productThumb'>
    {
      props.tag!==null && props.tag!==undefined &&
      <span className={`badge ${props.tag}`}>{props.tag}</span>
    }
        <div className='imgWrapper'>
          <img src={Product1} className='w-100' />
          <div className='overlay transition'>
            <ul className='list list-inline mb-0'>
              <li className='list-inline-item'>
                <a className='cursor' tooltip="Add to Wishlist">
                  <FavoriteBorderOutlinedIcon />
                </a>
              </li>
              <li className='list-inline-item'>
                <a className='cursor' tooltip="Compare">
                  <CompareArrowsOutlinedIcon />
                </a>
              </li>
              <li className='list-inline-item'>
                <a className='cursor' tooltip="Quick View">
                  <VisibilityOutlinedIcon />
                </a>
              </li>
            </ul>
          </div>
        </div>
     
      {/* Info Section Start */}
      <div className='info'>
        <span className='d-lock catName'>Jordan</span>
        <h4 className='title'><Link>Sign of passion and endurance.</Link></h4>
        <Rating name="half-rating-read" defaultValue={2.5} precision={0.5} readOnly />
        <span className='brand d-block text-g'>By<Link className='text-g mx-2'>Khichapokhari</Link></span>

        <div className='d-flex align-items-center w-100'>
          <div className='d-flex align-items-center'>
            <span className='price text-g fw-bold'>Rs.28.85</span> <span className='oldPrice'>Rs.32.89</span>
          </div>

          <Button className='bg-g ms-auto'><ShoppingCartOutlinedIcon />Add</Button>
        </div>
      </div>

    </div>
  )
}

export default Product
