import React from 'react';
import HomeSlider from './slider';
import CatSlider from '../../components/catSlider';
import Banners from '../../components/banners';
import './style.css'
import Product from '../../components/product';
import Banner4 from '../../assets/images/main-image.png';
import DailySlider from 'react-slick';
import TopProducts from './TopProducts';

const Home = () => {
  var settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 4,
    slidesToScroll: 1,
    fade: false,
    arrows: true,
    autoplay: 3000
  };
  return (
    <div>
      <HomeSlider />
      <CatSlider />
      <Banners />

      {/* Popular Products Starts here */}
      <section className='homeProducts'>
        <div className='container-fluid'>
          <div className='d-flex align-items-center'>
            <h2 className='hd mb-0 mt-0'>Popular Products</h2>
            <ul className='list list-inline ms-auto filterTab'>
              <li className='list-inline-item'>
                <a className='cursor'>All</a>
              </li>
              <li className='list-inline-item'>
                <a className='cursor'>Sleek</a>
              </li>
              <li className='list-inline-item'>
                <a className='cursor'>Sexy</a>
              </li>
              <li className='list-inline-item'>
                <a className='cursor'>Coverse</a>
              </li>
              <li className='list-inline-item'>
                <a className='cursor'>Beacon</a>
              </li>
              <li className='list-inline-item'>
                <a className='cursor'>Ringa</a>
              </li>
              <li className='list-inline-item'>
                <a className='cursor'>Jhitti</a>
              </li>
            </ul>
          </div>

          <div className='row productRow'>
            <div className='item'>
              <Product tag="sale" />
            </div>
            <div className='item'>
              <Product tag="hot" />
            </div>
            <div className='item'>
              <Product tag="new" />
            </div>
            <div className='item'>
              <Product tag="best" />
            </div>
            <div className='item'>
              <Product tag="hot" />
            </div>
            <div className='item'>
              <Product tag="new" />
            </div>
            <div className='item'>
              <Product tag="hot" />
            </div>
            <div className='item'>
              <Product tag="hot" />
            </div>
            <div className='item'>
              <Product tag="best" />
            </div>
            <div className='item'>
              <Product tag="hot" />
            </div>
          </div>

        </div>
      </section>
      {/* Popular Products Ends here */}


      {/* Daily Best Sells Starts here */}
      <section className='homeProducts homeProductsRow2 '>
        <div className='container-fluid'>
          <div className='d-flex align-items-center'>
            <h2 className='hd mb-0 mt-0'>Daily Best Sells</h2>
            <ul className='list list-inline ms-auto filterTab mb-0'>
              <li className='list-inline-item'>
                <a className='cursor'>Featured</a>
              </li>
              <li className='list-inline-item'>
                <a className='cursor'>Popular</a>
              </li>
              <li className='list-inline-item'>
                <a className='cursor'>New Added</a>
              </li>
            </ul>
          </div>

          <div className='row'>
            <div className='col-md-3'>
              <img src={Banner4} className='w-100' />
            </div>
            <div className='col-md-9'>
              <DailySlider {...settings} className='prodSlider'>
                <div className='item'>
                  <Product tag="sale" />
                </div>
                <div className='item'>
                  <Product tag="hot" />
                </div>
                <div className='item'>
                  <Product tag="sale" />
                </div>
                <div className='item'>
                  <Product tag="best" />
                </div>
                <div className='item'>
                  <Product tag="new" />
                </div>
              </DailySlider>

            </div>
          </div>
        </div>
      </section>
      {/* Daily Best Sells Ends here */}

      {/* Top Product Section Starts here */}
      <div className='topProductsSection'>
        <div className='container-fluid'>
          <div className='row'>
            <div className='col'>
              <TopProducts title="Top Selling" />
            </div>
            <div className='col'>
              <TopProducts title="Trending Products" />
            </div>
            <div className='col'>
              <TopProducts title="Recently Added" />
            </div>
            <div className='col'>
              <TopProducts title="Top Rated" />
            </div>
          </div>
        </div>
      </div>
      {/* Top Product Section Ends here */}
    </div>
  )
}

export default Home
