import React, { useState } from 'react';
import './style.css';
import Slider from 'react-slick';
import Goldstar1 from '../../assets/images/1.png';

const CatSlider = () => {

    const [itemBg, setItemBg] = useState([
        '#fffceb', '#ecffec', '#feefea', '#fff3eb', '#fff3ff', '#f2fce4', '#feefea', '#fffceb', '#ecffec', '#feefea', '#fff3eb', '#fff3ff', '#f2fce4', '#feefea', '#fffceb', '#ecffec', '#feefea', '#fffceb', '#ecffec'
    ]);


    var settings = {
        dots: false,
        infinite: true,
        speed: 500,
        slidesToShow: 8,
        slidesToScroll: 1,
        fade: false,
        arrows: true,
        autoplay: 2000,
        centerMode: true
    };
    return (
        <div className='catSliderSection'>
            <div className='container-fluid'>
                <h2 className='hd'>Featured Categories</h2>
                <Slider {...settings} className='cat_slider_Main'>
                    {
                        itemBg.length !== 0 && itemBg.map((item, index) => {
                            return (
                                <div className='item' key='index'>
                                    <div className='info' style={{background:item}}>
                                        <img src={Goldstar1} height={80} width={80} />
                                        <h5>Trekking1</h5>
                                        <p>27 items</p>
                                    </div>
                                </div>
                            )
                        })
                    }
                </Slider>
            </div>
        </div>
    )
}

export default CatSlider
