import React, { useEffect, useRef, useState } from 'react'
import './header.css';
import logo from '../../assets/images/logo.png'
import SearchIcon from '@mui/icons-material/Search';
import SelectDrop from '../selectDrop/selectDrop';
import axios from 'axios';
import ClickAwayListener from '@mui/material/ClickAwayListener';
import IconCompare from '../../assets/images/icon-compare.png';
import IconCart from '../../assets/images/icon-cart.png';
import IconHeart from '../../assets/images/icon-heart.png';
import IconUser from '../../assets/images/icon-user.png';
import { Button } from '@mui/material';
import Person2OutlinedIcon from '@mui/icons-material/Person2Outlined';
import RoomOutlinedIcon from '@mui/icons-material/RoomOutlined';
import FavoriteBorderOutlinedIcon from '@mui/icons-material/FavoriteBorderOutlined';
import SettingsOutlinedIcon from '@mui/icons-material/SettingsOutlined';
import LogoutOutlinedIcon from '@mui/icons-material/LogoutOutlined';
import LocationOnOutlinedIcon from '@mui/icons-material/LocationOnOutlined';
import Nav from './nav/nav';



const Header = () => {

  const [isOpenDropDown, setisOpenDropDown] = useState(false);
  const headerRef = useRef();

  const [categories, setcategories] = useState([
    'Trekking',
    'Sports',
    'Casual',
    'Converse',
    'Sneakers',
    'Jordan'
  ]);

  const countryList = [];

  useEffect(() => {
    getCountry('https://countriesnow.space/api/v0.1/countries/');
  }, []);

  const getCountry = async (url) => {
    try {
      await axios.get(url).then((res) => {
        if (res !== null) {
          res.data.data.map((item, index) => {
            countryList.push(item.country);
          })
        }
      })

    } catch (error) {
      console.log(error.message);
    }
  }

  useEffect(() => {
    window.addEventListener("scroll", () => {
      let position = window.pageYOffset;
      if (position > 100) {
        headerRef.current.classList.add('fixed');
      } else {
        headerRef.current.classList.remove('fixed');
      }
    });
  }, []);

  return (
    <>
      <div className='headerWrapper' ref={headerRef}>
        <header>
          <div className='container-fluid'>
            <div className='row'>
              <div className='col-sm-1'>
                <img src={logo} className='logoSize'/>
              </div>

              {/* Header Search Starts Here */}
              <div className='col-sm-5'>
                <div className='headerSearch d-flex align-items-center'>
                  <SelectDrop data={categories} placeholder={'All Categories'} icon={false} />
                  <div className='search'>
                    <input type='text' placeholder='Search for Items...' />
                  </div>
                  <SearchIcon className="searchIcon cursor" />
                </div>
              </div>
              {/* Header Search Ends Here */}


              <div className='col-sm-6 d-flex align-items-center'>
                <div className='ml-auto d-flex align-items-center'>
                  {/* Country Option  */}
                  <div className='countryWrapper'>
                    <SelectDrop data={countryList} placeholder={'Your Location'} icon={<LocationOnOutlinedIcon style={{ opacity: '0.7' }} />} />
                  </div>

                  {/* Starting Menu Lists */}
                  <ClickAwayListener onClickAway={() => setisOpenDropDown(false)}>
                    <ul className='list list-inline mb-0 headerTabs'>
                      <li className='list-inline-item'>
                        <span>
                          <img src={IconCompare} style={{ height: '20px', width: '20px', marginRight: '5px' }} />
                          <span className='badge bg-success rounded-circle'>3</span>
                          Compare
                        </span>
                      </li>
                      <li className='list-inline-item'>
                        <span>
                          <img src={IconHeart} style={{ height: '20px', width: '20px', marginRight: '5px' }} />
                          <span className='badge bg-success rounded-circle'>3</span>
                          Wishlist
                        </span>
                      </li>
                      <li className='list-inline-item'>
                        <span>
                          <img src={IconCart} style={{ height: '20px', width: '20px', marginRight: '5px' }} />
                          <span className='badge bg-success rounded-circle'>3</span>
                          Cart
                        </span>
                      </li>
                      {/* Account Dropdown */}
                      <li className='list-inline-item'>
                        <span onClick={() => setisOpenDropDown(!isOpenDropDown)}>
                          <img src={IconUser} style={{ height: '20px', width: '20px', marginRight: '5px' }} />
                          Account
                        </span>
                        {
                          isOpenDropDown !== false &&
                          <ul className='dropdownMenu'>
                            <li><Button><Person2OutlinedIcon />My Account</Button></li>
                            <li><Button><RoomOutlinedIcon />Order Tracking</Button></li>
                            <li><Button><FavoriteBorderOutlinedIcon />My Wishlist</Button></li>
                            <li><Button><SettingsOutlinedIcon />Setting</Button></li>
                            <li><Button><LogoutOutlinedIcon />Sign Out</Button></li>
                          </ul>
                        }
                      </li>
                    </ul>
                    {/* Ending Menu Lists */}
                  </ClickAwayListener>
                </div>
              </div>
            </div>
          </div>
        </header>
        <Nav />
      </div>
      <div className='afterHeader'></div>
    </>
  )
}

export default Header
